var greeting = function (message, name) {
    return message + name;
}

var arrowGreeting = (message, name) => {
    return message + name;
}

var arrowExpression = (message, name) => message + name;


greeting("Hello ", "Mayur");
arrowExpression("Hello there ", "mayur");